import React from "react";
import { NavLink } from "react-router-dom";
import styles from "./Nav.module.scss";

function Nav() {
  return (
    <nav
      className={styles.nav}
    >
      <ul>
        <li>
          <NavLink to="/"></NavLink>
        </li>
        <li>
          <NavLink to="/about"> </NavLink>
        </li>
        <li>
          <NavLink to="/projects"></NavLink>
        </li>
        <li>
          <NavLink to="/science"> </NavLink>
        </li>
      </ul>
    </nav>
  );
}

export { Nav };