import React from "react";
import styles from "./ModalNav.module.scss";
import { Link } from "react-router-dom";

const ModalNav = () => {
  return (
    <nav className={styles.nav}>
      <Link to="/"></Link>
      <Link to="/about"> </Link>
      <Link to="/projects"></Link>
      <Link to="/science"> </Link>
      <Link to="/contacts"></Link>
    </nav>
  );
};

export { ModalNav };
